import React from 'react'
import { Link } from 'gatsby'

export default function Pagination({ page, pagesCount, hrefBuilder }) {
  return (
    <nav className="relative z-0 inline-flex shadow-sm p-4">
      {Array.from({ length: pagesCount }, (i, idx) => (
        <Link
          key={idx}
          to={hrefBuilder(idx + 1)}
          className={`-ml-px relative inline-flex items-center px-4 py-2 border border-gray-300${
            page === idx + 1 ? ' bg-gray-400' : ' bg-white'
          } text-sm leading-5 font-medium text-gray-700 hover:text-gray-500 focus:z-10 focus:outline-none focus:border-blue-300 focus:shadow-outline-blue active:bg-gray-100 active:text-gray-700 transition ease-in-out duration-150`}
        >
          {idx + 1}
        </Link>
      ))}
    </nav>
  )
}
