import React from 'react'
import { graphql } from 'gatsby'
import { MDXRenderer } from 'gatsby-plugin-mdx'

import Layout from '../components/Layout'
import SEO from '../components/SEO'
import PostPreview from '../components/PostPreview'
import Pagination from '../components/Pagination'

export const query = graphql`
  query CategoryBySlug($slug: String!, $skip: Int!, $limit: Int!) {
    category: mdx(
      slug: { eq: $slug }
      fileAbsolutePath: { regex: "/categories/" }
    ) {
      body
      excerpt
      frontmatter {
        name
        title
      }
    }
    posts: allMdx(
      filter: {
        fileAbsolutePath: { regex: "/posts/" }
        frontmatter: { category_slug: { eq: $slug } }
      }
      sort: { fields: frontmatter___date }
      skip: $skip
      limit: $limit
    ) {
      edges {
        node {
          slug
          excerpt
          frontmatter {
            title
            thumbnail {
              childImageSharp {
                fluid {
                  ...GatsbyImageSharpFluid
                }
              }
            }
            category_slug
          }
        }
      }
    }
  }
`

export default function CategoryPage({ data, pageContext }) {
  const { category, posts } = data
  const { slug, page, pagesCount } = pageContext

  function hrefBuilder(page) {
    return `/category/${slug}${page === 1 ? '' : `/${page}`}`
  }

  return (
    <Layout>
      <SEO title={category.frontmatter.title} description={category.excerpt} />
      <h1>{category.frontmatter.name}</h1>
      <MDXRenderer>{category.body}</MDXRenderer>
      <div style={{ display: 'flex', flexWrap: 'wrap' }}>
        {posts.edges.map(edge => (
          <PostPreview key={edge.node.slug} post={edge.node} />
        ))}
      </div>
      <Pagination
        page={page}
        pagesCount={pagesCount}
        hrefBuilder={hrefBuilder}
      />
    </Layout>
  )
}
